/* skipnavigation */
.skip-navi {
  font-size: 0;
  line-height: 0;
  overflow: hidden; }

.skip-navi a {
  display: block;
  width: 100%;
  padding: 10px 0;
  color: #fff;
  position: absolute;
  left: 0;
  top: -100px;
  text-align: center;
  z-index: 200;
  background: #413022;
  border: 0 none; }

.skip-navi a:focus,
.skip-navi a:hover,
.skip-navi a:active {
  top: 0;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.5;
  border-bottom: 1px solid #fff; }

/* notice */
body.ie .notice_wrap {
  display: block; }

.notice_wrap {
  display: none;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  padding: 0.5% 0;
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(GradientType=1, StartColorStr='#7F000000', EndColorStr='#7F000000')";
  filter: progid:DXImageTransform.Microsoft.Gradient(GradientType=1, StartColorStr='#7F000000', EndColorStr='#7F000000');
  background: rgba(0, 0, 0, 0.5);
  color: #fff;
  text-align: center;
  letter-spacing: -1px;
  z-index: 100; }

.notice_wrap > p {
  font-size: 16px;
  font-weight: bold; }

/* selection */
::selection {
  background: #e9b2cb;
  color: #fff;
  text-shadow: none; }

::-moz-selection {
  background: #e9b2cb;
  color: #fff;
  text-shadow: none; }

::-webkit-selection {
  background: #e9b2cb;
  color: #fff;
  text-shadow: none; }

button:active {
  outline: none; }

button span {
  position: relative; }
